import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from 'react-router-dom';
import { getTVSearch } from "../redux/asynch/getSearchResult";
import { Link as ScrollLink } from 'react-scroll';

export default function SearchTVResult (props) {
    const [page, setPage] = useState(1);
    const dispatch = useDispatch()
    const searchResult = useSelector(state => state.searchResult.tv)

    useEffect(() => {
        dispatch(getTVSearch(props.searchRequest))// eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.searchRequest, page])

    const movieCard = (item) => {
        return (
            <div className='col-lg-6' key={item.id}>
                <div className='board'>
                    <div className='row'>
                        <div className='col-4'>
                            <div className='board__image-wrapper'>
                                {item.poster_path ?
                                    <img
                                        className='board__image'
                                        src={`https://www.themoviedb.org/t/p/w600_and_h900_bestv2${item.poster_path}`}
                                        alt={item.title}
                                    /> :
                                    <img
                                        className='board__image'
                                        src={`https://image.shutterstock.com/image-vector/vector-graphic-no-thumbnail-symbol-260nw-1391095985.jpg`}
                                        alt={item.title}
                                    />
                                }
                            </div>
                        </div>
                        <div className='col-8'>
                            <div className='board__info'>
                            <h3 className='board__title'>{item.name}</h3>
                            <p className='board__overview'>
                                {item.overview.slice(0, 150)}<span>...<Link className='board__link' to={`/the-movie-db/TVseries/${item.id}`}>Read more</Link></span>
                            </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    const Pagination = () => {
        let content = [];
        for(let i = 1; i <= searchResult.total_pages; i++) {
            content.push(
                <button
                    className='pagination__item'
                    key={i}
                    onClick={() => setPage(i)}
                    disabled={i === page ? true : false}>
                        {i}
                </button>
            )
        }
        return content
    }

    return (
        <div className='search__results' id="TVs-result">
            <div className='search__heading'>
                <h2>TV series</h2>
                <ScrollLink 
                    to='movies-result'
                    smooth='true'
                    duration={500}
                    offset={-34}
                    className='search__scroll'
                    activeClass='scroll-active'
                >
                    Movies &#9650;
                </ScrollLink>
            </div>
            <div className='search__movies'>
                <div className='row wrap'>
                    {searchResult.results?
                    searchResult.results.map(item => movieCard(item))
                    : null
                    }
                </div>
            </div>
            <div className='search__pagination pagination'>
                <Pagination />
            </div>
        </div>
    )
}